import React, { Component } from 'react';
import { connect } from 'react-redux';
import asyncComponent from './hoc/asyncComponent/asyncComponent';

import PrivateRoute from './pages/Auth/PrivateRoute/PrivateRoute';
import Layout from './hoc/Layout/Layout';
import Logout from './pages/Auth/Logout/Logout';
import NotFound from './pages/Auth/NotFound/NotFound';
import * as actions from './store/actions/index';
import { Route, Switch, Redirect, withRouter } from 'react-router-dom';

const asyncLogin = asyncComponent(() => {
  return import('./pages/Auth/Login');
});

const asyncProfileConfig = asyncComponent(() => {
  return import('./pages/ProfileConfig/ProfileConfig');
});

const asyncChangePasssword = asyncComponent(() => {
  return import('./pages/ProfileConfig/ChangePassword/ChangePassword');
});

const asyncUsersOverview = asyncComponent(() => {
  return import('./pages/Users/UsersOverview');
});

const asyncCoursesOverview = asyncComponent(() => {
  return import('./pages/Courses/CoursesOverview');
});

const asyncCourseEnrollment = asyncComponent(() => {
  return import('./pages/CourseEnrollment/CourseEnrollment');
});

const asyncMyCourses = asyncComponent(() => {
  return import('./pages/MyCourses/MyCourses');
});

const asyncAttendancePartaker = asyncComponent(() => {
  return import('./pages/AttendanceRegister/Partaker/AttendancePartaker');
});

const asyncInstructorCourses = asyncComponent(() => {
  return import('./pages/InstructorCourses/InstructorCourses');
});

const asyncAttendanceMenuInstructor = asyncComponent(() => {
  return import(
    './pages/AttendanceRegister/Instructor/AttendanceMenuInstructor'
  );
});

const asyncAttendanceInstructor = asyncComponent(() => {
  return import('./pages/AttendanceRegister/Instructor/AttendanceInstructor');
});

const asyncPartakersList = asyncComponent(() => {
  return import('./pages/InstructorCourses/PartakerList/PartakerList');
});

const asyncGradesList = asyncComponent(() => {
  return import('./pages/InstructorCourses/GradesList/GradesList');
});

const asyncFileExplorer = asyncComponent(() => {
  return import('./pages/FileExplorer/FileExplorer');
});

const asyncCharts = asyncComponent(() => {
  return import('./pages/Charts/Charts');
});

class App extends Component {
  componentDidMount() {
    this.props.onTryAutoSignup();
  }

  render() {
    let routes = (
      <Switch>
        <Route exact path='/'>
          {' '}
          <Redirect to='/Login' />{' '}
        </Route>
        <Route exact path='/Login' component={asyncLogin} />
        <PrivateRoute
          exact
          path='/profile-config'
          component={asyncProfileConfig}
        />
        <PrivateRoute
          exact
          path='/profile-config/change-password'
          component={asyncChangePasssword}
        />
        {/* BORRAR **************************************************/}
        <PrivateRoute path='/users-overview' component={asyncUsersOverview} />
        <PrivateRoute
          path='/courses-overview'
          component={asyncCoursesOverview}
        />
        <PrivateRoute path='/file-explorer' component={asyncFileExplorer} />
        <PrivateRoute path='/charts' component={asyncCharts} />

        {/* BORRAR **************************************************/}

        <PrivateRoute
          path='/course-enrollment'
          component={asyncCourseEnrollment}
        />

        {/* Cursos del participante, asistencia, evaluacion, descargar cedula, dar de baja*/}
        <PrivateRoute path='/mycourses' component={asyncMyCourses} />
        <PrivateRoute
          path='/attendance-partaker/:group'
          component={asyncAttendancePartaker}
        />

        {/* Cursos del instructor, asistencia, lista de inscritos y calificaciones*/}
        <PrivateRoute
          path='/instructor-courses/'
          component={asyncInstructorCourses}
        />
        <PrivateRoute
          path='/attendance-menu-instructor/:group'
          component={asyncAttendanceMenuInstructor}
        />
        <PrivateRoute
          path='/attendance-instructor/:group/:date'
          component={asyncAttendanceInstructor}
        />
        <PrivateRoute
          path='/partakers-list/:group'
          component={asyncPartakersList}
        />

        <PrivateRoute path='/grades-list/:group' component={asyncGradesList} />

        <PrivateRoute path='/logout' component={Logout} />
        {/* Muestra mensaje cuando se quiera dirigir a una pagina que no existe*/}
        <PrivateRoute component={NotFound} />
      </Switch>
    );

    if (this.props.userRole === 'COORDINADOR') {
      routes = (
        <Switch>
          <Route exact path='/'>
            {' '}
            <Redirect to='/Login' />{' '}
          </Route>
          <Route exact path='/Login' component={asyncLogin} />
          {/* MODULOS PARA EL COORDINADOR*/}
          <PrivateRoute path='/users-overview' component={asyncUsersOverview} />
          <PrivateRoute
            path='/courses-overview'
            component={asyncCoursesOverview}
          />
          <PrivateRoute path='/file-explorer' component={asyncFileExplorer} />
          <PrivateRoute path='/charts' component={asyncCharts} />
          {/* Configuraci√≥n de usuario*/}
          <PrivateRoute
            exact
            path='/profile-config'
            component={asyncProfileConfig}
          />
          <PrivateRoute
            exact
            path='/profile-config/change-password'
            component={asyncChangePasssword}
          />
          {/* Inscripciones*/}

          <PrivateRoute
            path='/course-enrollment'
            component={asyncCourseEnrollment}
          />

          {/* Cursos del participante, asistencia, evaluacion, descargar cedula, dar de baja*/}
          <PrivateRoute path='/mycourses' component={asyncMyCourses} />
          <PrivateRoute
            path='/attendance-partaker/:group'
            component={asyncAttendancePartaker}
          />

          {/* Cursos del instructor, asistencia, lista de inscritos y calificaciones*/}
          <PrivateRoute
            path='/instructor-courses/'
            component={asyncInstructorCourses}
          />
          <PrivateRoute
            path='/attendance-menu-instructor/:group'
            component={asyncAttendanceMenuInstructor}
          />
          <PrivateRoute
            path='/attendance-instructor/:group/:date'
            component={asyncAttendanceInstructor}
          />
          <PrivateRoute
            path='/partakers-list/:group'
            component={asyncPartakersList}
          />
          <PrivateRoute
            path='/grades-list/:group'
            component={asyncGradesList}
          />
          <PrivateRoute path='/logout' component={Logout} />
          {/* Muestra mensaje cuando se quiera dirigir a una pagina que no existe*/}
          <PrivateRoute component={NotFound} />
        </Switch>
      );
    }

    return (
      <div>
        <Layout>{routes}</Layout>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    userRole: state.auth.userRole,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onTryAutoSignup: () => dispatch(actions.authCheckState()),
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(App));
